import React, { Component } from 'react'
import { EditorState, ContentState, convertToRaw, convertFromRaw } from 'draft-js'
import Editor from './components/Editor'
import { VK, Comments, Like } from 'react-vk';
import { VKShareButton,VKShareCount,VKIcon } from 'react-share';
import './App.css'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      title: '   ...',
      author: '  ',
      editorState: EditorState.createEmpty(),
      canEdit: false,
    }
    this.onTitleChange = (e) => this.setState({ title: e.target.value })
    this.onAuthorChange = (e) => this.setState({ author: e.target.value })
    this.onEditorChange = this.onEditorChange.bind(this)
    this.saveContent = this.saveContent.bind(this)
  }

  saveContent (content){
    window.localStorage.setItem('content', JSON.stringify(convertToRaw(content)))
  }
  
  onEditorChange(editorState) { 
    this.saveContent(editorState.getCurrentContent())
    this.setState({ editorState })
  }

  handleNewComment(num, last_comment, date, sign){
    console.log(last_comment);
  }

  componentWillMount() {
    const content = window.localStorage.getItem('content');

    if (content) {
      try {
        console.log(content)
        this.state.editorState = EditorState.createWithContent(convertFromRaw(JSON.parse(content)));
      } catch (err) {
        this.state.editorState = EditorState.createWithContent(ContentState.createFromText(`   -  ... :c\n ${err}`))
      }

    } else {
      this.state.editorState = EditorState.createWithContent(convertFromRaw(JSON.parse({})));
    }
  }

  render() {
    const shareUrl = 'http://wazzap.us';
    const title = 'WAZAP';

    return (
      <div className="App">
        <main className="App-main">
          <header className="App-header">
            <h1>
              {this.state.title && <label htmlFor="title">Title</label>}
              <input
                id="title"
                type="text"
                className="input__lg"
                value={this.state.title}
                onChange={this.onTitleChange}
                placeholder="Title"
                autoComplete="off"
                readOnly={!this.state.canEdit}
                
              />
            </h1>
            <address>
              {this.state.author && <label htmlFor="author">Author</label>}
              <input
                id="author"
                type="text"
                className="input__sm"
                value={this.state.author}
                onChange={this.onAuthorChange}
                placeholder="Your name"
                autoComplete="off"
                readOnly={!this.state.canEdit}
                
              />
            </address>
          </header>
          <article className="App-article">
            <Editor
              editorState={this.state.editorState}
              onChange={this.onEditorChange}
              placeholder="     ..."
            />
          
          <VKShareButton
            url={shareUrl}
            image={`${String(window.location)}/`}
            windowWidth={660}
            windowHeight={460}
            className="Demo__some-network__share-button">
            <VKIcon
              size={24}
              round />
              <VKShareCount url={shareUrl}
            className="Demo__some-network__share-count" />
          </VKShareButton>

          

          <VK apiId={6457453}>
            <Comments onNewComment={this.handleNewComment} />
          </VK>

          
          </article>
        </main>
      </div>
    )
  }
}

export default App



